google.maps.event.addDomListener(window, "load", init);

function init() {
  let mapOptions = {
    zoom: 17,

    center: new google.maps.LatLng(55.176389425557275, -118.7894588927695),

    styles: [
      {
        featureType: "all",
        elementType: "geometry",
        stylers: [{ color: "#202c3e" }],
      },
      {
        featureType: "all",
        elementType: "labels.text.fill",
        stylers: [
          { gamma: 0.01 },
          { lightness: 20 },
          { weight: "1.39" },
          { color: "#ffffff" },
        ],
      },
      {
        featureType: "all",
        elementType: "labels.text.stroke",
        stylers: [
          { weight: "0.96" },
          { saturation: "9" },
          { visibility: "on" },
          { color: "#000000" },
        ],
      },
      {
        featureType: "all",
        elementType: "labels.icon",
        stylers: [{ visibility: "off" }],
      },
      {
        featureType: "landscape",
        elementType: "geometry",
        stylers: [{ lightness: 30 }, { saturation: "9" }, { color: "#29446b" }],
      },
      {
        featureType: "poi",
        elementType: "geometry",
        stylers: [{ saturation: 20 }],
      },
      {
        featureType: "poi.park",
        elementType: "geometry",
        stylers: [{ lightness: 20 }, { saturation: -20 }],
      },
      {
        featureType: "road",
        elementType: "geometry",
        stylers: [{ lightness: 10 }, { saturation: -30 }],
      },
      {
        featureType: "road",
        elementType: "geometry.fill",
        stylers: [{ color: "#193a55" }],
      },
      {
        featureType: "road",
        elementType: "geometry.stroke",
        stylers: [{ saturation: 25 }, { lightness: 25 }, { weight: "0.01" }],
      },
      {
        featureType: "water",
        elementType: "all",
        stylers: [{ lightness: -20 }],
      },
    ],
  };

  let mapElement = document.querySelector(".js-map");

  let map = new google.maps.Map(mapElement, mapOptions);

  const image = "./images/maps/1.svg";
  let marker = new google.maps.Marker({
    position: new google.maps.LatLng(55.176389425557275, -118.7894588927695),
    map: map,
    title: "Snazzy!",
    icon: image,
  });
}
