const swiperOurWorks = new Swiper(".swiper-our-works", {
  direction: "horizontal",
  slidesPerView: 1,

  breakpoints: {
    768: {
      slidesPerView: 1,
      spaceBetween: 45,
    },
    320: {
      slidesPerView: 1,
      spaceBetween: 45,
    },

    navigation: {
      nextEl: ".swiper-our-works .swiper-button-prev-our-works",
      prevEl: ".swiper-our-works .swiper-button-next-our-works",
    },
  },
});
